<template>
    <div class="slider" ref="slider">
        <div class="slider-group" ref="sliderGroup">
            <slot></slot>
        </div>
        <div class="dots">
            <span
                class="dot"
                v-for="(item, index) in dots"
                :key="index"
                :class="{active: currentPageIndex === index}"
            ></span>
        </div>
    </div>
</template>

<script>
import BScroll from "better-scroll";
import { addClass } from "common/js/dom";

export default {
    data() {
        return {
            dots: [],
            currentPageIndex: 0
        };
    },
    props: {
        loop: {
            type: Boolean,
            default: true
        },
        autoPlay: {
            type: Boolean,
            default: true
        },
        interval: {
            type: Number,
            default: 4000
        },
        threshold: {
            type: Number,
            default: 0.3
        },
        speed: {
            type: Number,
            default: 300
        }
    },
    mounted() {
        setTimeout(() => {
            this._setSliderWidth();
            this._initDots();
            this._initSlider();
        }, 20);

        window.addEventListener('resize', this._refresh);
    },
    destroyed() {
        clearTimeout(this.timer);
    },
    methods: {
        _refresh() {
            if(this.slider) {
                this._setSliderWidth(true);
                this.slider.refresh();
            }
        },
        _setSliderWidth(isResize) {
            this.children = this.$refs.sliderGroup.children;

            let width = 0; // 作为轮播图容器宽度
            let sliderWidth = this.$refs.slider.clientWidth; // 轮播图item的宽度

            for (let i = 0; i < this.children.length; i++) {
                let child = this.children[i];
                addClass(child, "slider-item");

                child.style.width = sliderWidth + "px"; //为每张图片设置宽度
                width += sliderWidth;
            }

            if (this.loop && !isResize) {
                width += 2 * sliderWidth; // 前后各加一张图实现循环轮播
            }

            this.$refs.sliderGroup.style.width = width + "px";
        },
        _initDots() {
            this.dots = new Array(this.children.length);
        },
        _initSlider() {
            this.slider = new BScroll(this.$refs.slider, {
                scrollX: true,
                scrollY: false,
                momentum: false,
                snap: {
                    loop: this.loop,
                    threshold: this.threshold,
                    speed: this.speed
                },
                bounce: false
            });

            this.slider.on("scrollEnd", this._onScrollEnd);

            if (this.autoPlay) this._play();

            this.slider.on("touchEnd", () => {
                if (this.autoPlay) {
                    clearTimeout(this.timer);
                }
            });

            this.slider.on("beforeScrollSatrt", () => {
                if(this.autoPlay) clearTimeout(this.timer);
            });
        },
        _onScrollEnd() {
            let pageIndex = this.slider.getCurrentPage().pageX;
            this.currentPageIndex = pageIndex;

            if (this.autoPlay) this._play();
        },
        _play() {
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.slider.next(this.speed);
            }, this.interval);
        }
    }
};
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/variable';

.slider {
    min-height: 1px;
    position: relative;
    width: 100%;
    overflow hidden;
    .slider-group {
        position: relative;
        overflow: hidden;
        white-space: nowrap;

        .slider-item {
            float: left;
            box-sizing: border-box;
            overflow: hidden;
            text-align: center;

            a {
                display: block;
                width: 100%;
                overflow: hidden;
                text-decoration: none;
            }

            img {
                display: block;
                width: 100%;
            }
        }
    }

    .dots {
        position: absolute;
        right: 0;
        left: 0;
        bottom: 12px;
        text-align: center;
        font-size: 0;

        .dot {
            display: inline-block;
            margin: 0 4px;
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background: $color-text-l;

            &.active {
                width: 20px;
                border-radius: 5px;
                background: $color-text-ll;
            }
        }
    }
}
</style>


